:root {
  --dark: #674188;
  --medium: #c3acd0;
  --light: #f7efe5;
  --background: #fffbf5;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: #fff;
  scroll-behavior: smooth;
}
body{
  overflow-x: hidden;
}
.about,
.project,
.intro {
  min-height: 500px;
}
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;
  font-size: 1.5rem;
  font-weight: bold;
  width: 100%;
  max-width: 1100px;
  margin: auto;
}
.active {
  color: var(--dark);
}
.underline {
  width: 60vw;
  height: 3px;
  background-color: var(--dark);
  margin: auto;
  border-radius: 10px;
}

ul {
  list-style-type: none;
}
a {
  text-decoration: none;
  color: black;
}
.logo span {
  display: inline-block;
  background-color: var(--dark);
  height: 20px;
  width: 20px;
  animation: react 5s infinite linear;
}

.logo:hover span {
  border-radius: 50%;
}

.links ul {
  display: flex;
  gap: 20px;
}
.links a:hover {
  opacity: 0.7;
  color: var(--dark);
  cursor: pointer;
}
.intro {
  display: grid;
  grid-template-columns: 2fr 1fr;
  justify-content: center;
  align-items: center;
  height: 90vh;
  width: 100%;
  margin: auto;
  border-bottom: 3px solid var(--dark);
  max-width: 1100px;
}

.root {
  display: flex;
  justify-content: center;
  position: relative;
}
.flexcontaine {
  width: 200px;
  height: 200px;
  background-color: var(--dark);
  border-radius: 50%;
  align-self: center;
  position: relative;
  box-shadow: 1px 1px 10px 1px #000;
}
.circle11 {
  width: 180px;
  height: 180px;
  background-color: var(--dark);
  align-self: center;
  border-radius: 50%;
  position: absolute;
  top: 9px;
  left: 9px;
  box-shadow: 1px 1px 10px 1px #000;
  border: 1px solid var(--background);
}
.circle12 {
  width: 160px;
  height: 160px;
  background-color: var(--dark);
  border-radius: 50%;
  position: absolute;
  top: 9px;
  left: 9px;
  border: 1px solid var(--background);
  box-shadow: 1px 1px 10px 1px #000;
}
.circle13 {
  width: 140px;
  height: 140px;
  background-color: var(--dark);
  border-radius: 50%;
  position: absolute;
  top: 9px;
  left: 9px;
  border: 1px solid var(--background);
  box-shadow: 1px 1px 10px 1px #000;
}
.circle14 {
  width: 120px;
  height: 120px;
  background-color: var(--dark);
  border-radius: 50%;
  position: absolute;
  top: 9px;
  left: 9px;
  border: 1px solid var(--background);
  box-shadow: 1px 1px 10px 1px #000;
  display: flex;
  justify-content: center;
  align-items: center;
}



.flexcontaine,
.circle11,
.circle12,
.circle13,
.circle14 {
  animation: squarerotate 30s infinite linear;
}
.root:hover .flexcontaine,
.root:hover .circle11,
.root:hover .circle12,
.root:hover .circle13,
.root:hover .circle14 {
  animation-play-state: paused;
}
.circle14 div {
  width: 9px;
  height: 9px;
  position: relative;
  top: -21px;
  left: -23px;
  background-color: transparent;
  animation-play-state: paused;
  font-size: 2rem;
  color: var(--light);
}
.circle14 div span {
  background-color: transparent;
  animation: blink 2s infinite linear;
}

.info p {
  color: var(--dark);
  font-size: 3rem;
  font-weight: bold;
  transition: cubic-bezier(0.68, -0.55, 0.265, 1.55) 5s ease;
}
.info {
  line-height: 1.5;
}

.info h3,
.info h4 {
  font-size: 2rem;
}

.about {
  width: 100%;
  max-width: 1100px;
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
  align-items: center;
  text-transform: capitalize;
  gap: 20px;
  border-bottom: 3px solid var(--dark);
  padding-bottom: 140px;
}
.about h2 {
  color: var(--dark);
  line-height: 2;
  font-size: 2rem;
  font-weight: bold;
}

.about p {
  width: 80%;
  font-size: 1.3rem;
}
.skills {
  text-align: center;
}

.skills div {
  display: flex;
  align-items: center;
}

.skills img {
  width: 50px;
  height: 50px;
  align-self: center;
  background-color: transparent;
  text-align: center;
}
.skills h3 {
  color: var(--dark);
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 50px;
}

.project {
  width: 100%;
  max-width: 1100px;
  text-align: center;
  margin: 50px auto;
  border-bottom: 3px solid var(--dark);
  
}
.project h3 {
  color: var(--dark);
  font-size: 2rem;
  font-weight: bold;
}
.projectCards{
  width: 100%;
  height: 100%;
  margin: 30px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-content: space-between;
  align-items: center;
  justify-items: center;
  gap: 15px;
  padding: 10px;
}
.projectCards .card{
  width: 300px;
  height:400px;
  background-color: var(--light);
  border: 3px solid var(--dark);
  transition: all 0.3s ease;
}
.card img{
  width: 100%;
  height: 85%;
  border-bottom: 3px solid var(--dark);
}
.card a{
  background-color: var(--light);

}
.card h4{
  background-color: var(--light);
  padding: 10px;
  font-size: 1.3rem;
  text-align: left;
  text-decoration: underline;
  text-decoration-thickness: 2px;
  color: #674188;
}
.projectCards .card:hover{
  transform: translateY(-10px);
  cursor: pointer;
}
