.animationRight {
  animation: fadeInAnimationRigth ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}
.animationLeft {
  animation: fadeInAnimationLeft ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}
