.contact {
  width: 100%;
  max-width: 1100px;
  min-height: 300px;
  margin: 50px auto;
  text-align: center;
  display: grid;
  grid-template-columns: 1.2fr 2fr;
  justify-content: center;
  align-items: flex-start;
}
.contact h3 {
  margin-top: 10px;
  font-size: 2rem;
  color: var(--dark);
  font-weight: bold;
}
.contactinfo {
  padding-top: 20px;
}

.contactinfo h4,
.contactinfo h5,
.contactinfo h6 {
  padding: 10px;
  font-size: 1.2rem;
  color: var(--dark);
}
.contact form {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.contact form input {
  width: 500px;
  padding: 10px;
  margin: 10px;
  border: 2px solid var(--dark);
  background-color: var(--background);
}
.contact textarea {
  width: 500px;
  height: 200px;
  margin: 10px;
  resize: none;
  border: 2px solid var(--dark);
  background-color: var(--background);
  padding: 10px;
}
.dark-btn {
  width: 100px;
  margin-top: 50px;
  padding: 10px;
  border: 2px solid var(--dark);
  background-color: var(--dark);
  color: var(--light);
  font-weight: bold;
}
.contact form button {
  margin: 10px;
  width: 500px;
  padding: 10px;
  border: 2px solid var(--dark);
  background-color: var(--dark);
  color: var(--light);
  font-weight: bold;
}

.contact button:hover {
  opacity: 0.8;
  cursor: pointer;
}
.footer {
  width: 100%;
  background-color: var(--dark);
  margin: auto;
  color: var(--light);
}
.footerContainer {
  width: 50%;
  max-width: 1100px;
  margin: auto;
  background-color: var(--dark);
  color: var(--light);
  text-transform: uppercase;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footerContainer ul .footerLinks {
  display: flex;
  flex-direction: column;
  background-color: var(--dark);
  padding: 20px;
}
.footerContainer ul li,
.footerContainer h6,
.footerContainer ul li a {
  color: var(--light);
  background-color: var(--dark);
}
.footerContainer li {
  padding: 10px;
}
.footerContainer li a:hover {
  opacity: 0.5;
  cursor: pointer;
}
.footerContainer div {
  background-color: var(--dark);
  align-self: flex-start;
  padding: 10px;
}
.social li{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 5px;
}
.social li > :first-child {
  background-color: var(--dark);
}

.flexcontainer {
  display: flex;
  justify-content: center;
  align-items: center;
}
.art > :first-child {
  margin-bottom: 30px;
  width: 90px;
  height: 90px;
  animation: pendulam 3s infinite linear;
}


.circle1,
.circle2,
.circle3,
.circle4 {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: var(--dark);
  text-align: center;
  box-shadow: 0 100px 100px 1px var(--dark);
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--light);
  font-weight: bold;
}
.circle1 {
  animation: moveleft 3s infinite linear;
}
.circle4 {
  animation: moveright 3s infinite linear;
}


@media screen and (max-width:1000px) {
  .projectCards .card{
    width: 210px;
    height: 300px;
  }  
  .card h4{
    padding: 0 10px;
  }
}
@media screen and (max-width: 786px) {
  header {
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
    margin: auto;
    padding-top: 0;
  }

  .circle1,
  .circle2,
  .circle3,
  .circle4 {
    box-shadow: 0 50px 50px 1px var(--medium);
    width: 80px;
    height: 80px;
    font-weight: normal;
  }
  .art > :first-child {
    width: 80px;
    height: 80px;
  }

  header .logo {
    margin: 20px;
    padding: 10px;
  }

  .intro {
    grid-template-columns: 1fr;
    grid-template-rows: 2fr 1fr;
    height: 60vh;
    margin: auto;
    text-align: center;
    padding-bottom: 20px;
  }
  .about {
    grid-template-columns: 1fr;
    margin: auto;
    text-align: center;
    border: none;
    padding-top: 10px;
    padding-bottom: 30px;
    min-height: 90vh;
    border-bottom: 3px solid var(--dark);
  }
  .about p {
    width: 100%;
  }
  .skills div {
    justify-items: center;
    /* margin-bottom:20px ; */
    /* padding-bottom: 30px; */
  }
  .projectCards{
    grid-template-columns: 1fr 1fr;
  }
  
  .dash {
    order: 2;
  }
  .contact {
    grid-template-columns: 1fr;
    justify-items: center;
  }
  .contact .contactinfo {
    display: none;
  }
  .contact div {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-bottom: 20px;
  }
  .contact div button,
  .contact div h3 {
    margin-top: 0;
  }
  .footerContainer {
    width: 80%;
  }
  @keyframes moveleft {
    0% {
      transform: translate(0, 0);
      background-color: var(--dark);
      color: var(--light);
      box-shadow: 0 50px 50px 1px var(--medium);
    }
    25% {
      transform: translate(-30px, -20px);
      background-color: var(--medium);
      color: var(--dark);
      box-shadow: 0 50px 50px 1px #fff;
    }
    50% {
      transform: translate(0, 0);
      background-color: var(--dark);
      color: var(--light);
      box-shadow: 0 50px 50px 1px var(--medium);
    }
    100% {
      transform: translate(0, 0);
      background-color: var(--dark);
      color: var(--light);
      box-shadow: 0 50px 50px 1px var(--medium);
    }
  }
  @keyframes moveright {
    0% {
      transform: translate(0, 0);
      background-color: var(--dark);
      color: var(--light);
      box-shadow: 0 50px 50px 1px var(--medium);
    }
    50% {
      transform: translate(0, 0);
      background-color: var(--dark);
      color: var(--light);
      box-shadow: 0 50px 50px 1px var(--medium);
    }
    75% {
      transform: translate(30px, -20px);
      background-color: var(--medium);
      color: var(--dark);
      box-shadow: 0 50px 50px 1px #fff;
    }
    100% {
      transform: translate(0, 0);
      background-color: var(--dark);
      color: var(--light);
      box-shadow: 0 50px 50px 1px var(--medium);
    }
  }
}
@media screen and (max-width: 500px) {
  body {
    overflow-x: hidden;
  }
  header {
    flex-direction: column;
