@keyframes squarerotate {
  0% {
    border-radius: 50%;
    transform: rotate(0);
  }
  50% {
    border-radius: 0;
    transform: rotate(180deg);
  }

  100% {
    border-radius: 50%;
    transform: rotate(360deg);
  }
}

@keyframes blink {
  0% {
    color: var(--light);
  }
  50% {
    color: var(--dark);
  }
  100% {
    color: var(--light);
  }
}


@keyframes fadeInAnimationRigth {
  0% {
    opacity: 0.3;
    translate: 40px;
  }
  100% {
    translate: 0px;
    opacity: 1;
  }
}
@keyframes fadeInAnimationLeft {
  0% {
    opacity: 0.3;
    translate: -40px;
  }
  100% {
    translate: 0px;
    opacity: 1;
  }
}

@keyframes react {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}

@keyframes pendulam {
  0% {
    transform: rotate(0);
  }
  25% {
    transform: rotate(20deg);
  }
  50% {
    transform: rotate(0);
  }
  75% {
    transform: rotate(-20deg);
  }
  100% {
    transform: rotate(0);
  }
}

@keyframes moveleft {
  0% {
    transform: translate(0, 0);
    background-color: var(--dark);
    color: var(--light);
    box-shadow: 0 100px 100px 1px var(--dark);
  }
  25% {
    transform: translate(-30px, -20px);
    background-color: var(--medium);
    color: var(--dark);
    box-shadow: 0 100px 100px 1px #fff;
  }
  50% {
    transform: translate(0, 0);
    background-color: var(--dark);
    color: var(--light);
    box-shadow: 0 100px 100px 1px var(--dark);
  }
  100% {
    transform: translate(0, 0);
    background-color: var(--dark);
    color: var(--light);
    box-shadow: 0 100px 100px 1px var(--dark);
  }
}
@keyframes moveright {
  0% {
    transform: translate(0, 0);
    background-color: var(--dark);
    color: var(--light);
    box-shadow: 0 100px 100px 1px var(--dark);
  }
  50% {
    transform: translate(0, 0);
    background-color: var(--dark);
    color: var(--light);
    box-shadow: 0 100px 100px 1px var(--dark);
  }
  75% {
    transform: translate(30px, -20px);
    background-color: var(--medium);
    color: var(--dark);
    box-shadow: 0 100px 100px 1px #fff;
  }
  100% {
    transform: translate(0, 0);
    background-color: var(--dark);
    color: var(--light);
    box-shadow: 0 100px 100px 1px var(--dark);
  }
}
